import setAuthToken from  "../utils/setAuthToken";

import axios from "axios";
import jwt_decode from "jwt-decode";

import {
    CURATOR_LOGIN,
    CURATOR_LOAD,
    CURATOR_ERROR,
    CURATOR_LINKS,
    CURATOR_ADD_URL,
    CURATOR_LOGOUT
} from "./types";


export const loginCurator = (curatorDetails) => dispatch =>{
    dispatch({
        type : CURATOR_LOAD
    })

    axios.post("/api/curatorauth/login",curatorDetails).then(result=>{
        if(result.data.success){
          //  alert("Login true")

          const { token } = result.data;
        //set token to storage
        localStorage.setItem("jwtToken", token);

        //set token to auth header
        setAuthToken(token);

        //decode token to get user data
        const decoded = jwt_decode(token);

        dispatch({
          type: CURATOR_LOGIN,
          payload: decoded.users
        });

        }else{            
        dispatch({
            type : CURATOR_ERROR,
            payload : "AUTH ERROR"
        })
    }
            
    }).catch(err=>{

        dispatch({
            type : CURATOR_ERROR,
            payload : err.response.data.err
        })

        // console.log(err.response.data.err);
        
    })
}

export const getCurrentCurator = (curatorToken)=> dispatch=>{
    dispatch({
        type : CURATOR_LOAD
    })

    axios.get("/api/curatorauth/current",{
        headers :{authorization : curatorToken}
    }).then(res=>{

        //set token to storage
        localStorage.setItem("jwtToken", curatorToken);

        //set token to auth header
        setAuthToken(curatorToken);

        dispatch({
            type : CURATOR_LOGIN,
            payload : res.data
        })
        
    }).catch(err=>console.log(err))
}

export const getCuratorLinks = ()=>dispatch=>{
    dispatch({
        type : CURATOR_LOAD
    })

    

    axios.get("/api/url/mylinks").then(result=>{
        
        dispatch({
            type : CURATOR_LINKS,
            payload : result.data
        })
    }).catch(err=>dispatch({
        type : CURATOR_ERROR,
        payload : "Some Error Occoured"
    }))
}

export const addURL = (url)=>dispatch=>{
    dispatch({
        type : CURATOR_LOAD
    })

    axios.post("/api/url/addurl",url).then(result=>{
        if(!result.data.success){
            dispatch({
                type : CURATOR_ERROR,
                payload : "Error Occoured Check Link"
            })

            alert("Error Occoured Check Link");
        }else
        dispatch({
            type : CURATOR_LINKS,
            payload : result.data.data
        })
    }).catch(err=>dispatch({
        type : CURATOR_ERROR,
        payload : "Some Error Occoured"
    }))

}

export const logOutCurator = () => dispatch => {
    dispatch({
      type: CURATOR_LOAD
    });
  
    setAuthToken();
    localStorage.removeItem("jwtToken");
    dispatch({
      type: CURATOR_LOGOUT
    });
  };