import {
    CURATOR_LOGIN ,
    CURATOR_LOAD,
    CURATOR_ERROR,
    CURATOR_LINKS,
    CURATOR_ADD_URL,
    CURATOR_LOGOUT
}from "../actions/types";


const initialState = {
    isAuthenticated : false,
    curator : null,
    curatorLoading : false,
    curatorLinks : [],
    curatorError : null
}



export default(state = initialState,action)=>{

    switch(action.type){

        case CURATOR_LOAD : return{
            ...state,
            curatorLoading : true
        }
        
        case CURATOR_LOGIN : return{
            ...state,
            curatorLoading : false,
            curator : action.payload,
            isAuthenticated : true,
            curatorError : null
        }
        case CURATOR_ERROR : return{
            ...state,
            curatorLoading : false,
            curatorError : action.payload
        }
        case CURATOR_LINKS : return{
            ...state,
            curatorLoading : false,
            curatorLinks : action.payload,
            curatorError : null

        }
        case CURATOR_ADD_URL : return{
            ...state,
            curatorLoading : false,
            curatorLinks : action.payload,
            curatorError : null
        }

        case CURATOR_LOGOUT:return{
            ...state,
            isAuthenticated : false,
            curator : null,
            curatorLoading : false,
            curatorLinks : [],
            curatorError : null
        }

        default : return state
    }

}