import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";

import { loginCurator,getCurrentCurator } from "../../actions/curatorAction";

class login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isAuthenticated: false,
      email: "",
      password: "",
      error : null
    };
  }

  componentDidMount() {
    this.setState({
      isAuthenticated: this.props.curator.isAuthenticated
    });

    if(localStorage.jwtToken){
        // console.log(localStorage.jwtToken);
       //  console.log(axios.defaults.headers.common['Authorization']);
         
       this.props.getCurrentCurator(localStorage.jwtToken);
       
       }

  }

  formDataChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  login = e => {
    e.preventDefault();

    // this.setState({
    //     isAuthenticated : true
    // })
    const curatorDetails = {
      email: this.state.email,
      password: this.state.password
    };
    this.props.loginCurator(curatorDetails);
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps.curator) {
      this.setState({
        isAuthenticated: nextProps.curator.isAuthenticated
      });

      if(nextProps.curator.curatorError){
        this.setState({
            error : nextProps.curator.curatorError
        })
    }
    }


  }

  render() {
    if (!this.state.isAuthenticated)
      return (
        <div className="login-wrapper">
          <div className="login-body">
            <div className="login-body-circle login-body-circle-1" />
            <div className="login-body-circle login-body-circle-2" />
          </div>
          <div className="login-content">
            <div className="login-content-left">
              <div className="login-content-left-logo">
                <img src={require("../../images/logo.png")} alt="" />
              </div>

              <form className="login-content-left-form" onSubmit={this.login}>
                <label htmlFor="login-form-email">E-mail</label>
                <input
                  type="email"
                  name="email"
                  id="login-form-email"
                  value={this.state.email}
                  onChange={this.formDataChange}
                />
                <label htmlFor="login-form-password">Password</label>
                <input
                  type="password"
                  name="password"
                  id="login-form-password"
                  value={this.state.password}
                  onChange={this.formDataChange}
                />

                <button type="submit">LOGIN</button>
              </form>
            </div>

            <div className="login-content-right">
              <span>MANAGE AND SHORTEN YOUR LINKS</span>
            </div>
          {this.state.error ? (
                <div className="alert alert-danger" role="alert">
                {this.state.error}
               </div>
          ) :null}
          </div>
          
        </div>
      );
    else {
      return <Redirect to="/dashboard" />;
    }
  }
}

const mapStateToProps = state => ({
  curator: state.curator
});

export default connect(
  mapStateToProps,
  { loginCurator ,
getCurrentCurator}
)(login);
