import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChartLine,
  faCompass,
  faLink,
  faSearch
} from "@fortawesome/free-solid-svg-icons";

import ViewLink from "./viewLink/viewLink";

import {
  getCurrentCurator,
  getCuratorLinks,
  addURL,
  logOutCurator
} from "../../actions/curatorAction";
import { log } from "util";

class dashboard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isAuthenticated: false,
      viewLink: false,
      curator: null,
      myLinks : [],
      selectedLink : null,
      newLink : "",
      search : "",
      searchLinks : []
    };
  }

  componentDidMount() {
    this.setState({
      isAuthenticated: this.props.curator.isAuthenticated
    });

    if (localStorage.jwtToken) {
      // console.log(localStorage.jwtToken);
      //  console.log(axios.defaults.headers.common['Authorization']);

      this.props.getCurrentCurator(localStorage.jwtToken);
    }

    if (this.props.curator.isAuthenticated) {
      this.props.getCuratorLinks();
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.curator) {
      this.setState({
        isAuthenticated: nextProps.curator.isAuthenticated
      });

      if (nextProps.curator.curator) {
        this.setState({
          curator: nextProps.curator.curator
        });
      }

      if(nextProps.curator.curatorLinks){
        this.setState({
          myLinks : nextProps.curator.curatorLinks
        })
      }

    }
  }

  viewLinkStats = (linkData) => {
    this.setState({
      viewLink: !this.state.viewLink,
      selectedLink : linkData
    });
  };


  topLinks = ()=>{
    let topLink = this.state.myLinks;
   topLink = topLink.map(linkData=>{
      let views = linkData.accessed.length;
      return{
        ...linkData,
        views
      }
    })
    

    var sort_by = function(field, reverse, primer){

      var key = primer ? 
          function(x) {return primer(x[field])} : 
          function(x) {return x[field]};
   
      reverse = !reverse ? 1 : -1;
   
      return function (a, b) {
          return a = key(a), b = key(b), reverse * ((a > b) - (b > a));
        } 
   }

   topLink.sort(sort_by('views',true,parseInt));
 

   
return topLink;
  }

  addLink = ()=>{
    this.props.addURL({longUrl : this.state.newLink})
  }

  logout = () => {
    this.props.logOutCurator();
  };

  searchLinksChange = e=>{
    this.setState({
      search : e.target.value
    })
  }

  searchLinks = ()=>{
    let allLinks = this.state.myLinks;
     allLinks = allLinks.filter(linkData=>{
      return linkData.longUrl.includes(this.state.search);
    })

    this.setState({
      searchLinks : allLinks
    })
   
  }

  render() {

    let totalViews = 0;
     this.state.myLinks.map(linkData=>{
        totalViews += linkData.accessed.length
    })


    return (
      <>
        {!this.props.curator.isAuthenticated ? <Redirect to="/" /> : null}
        {this.props.curator.curatorLoading ? ( <h1>LOADING ....</h1> ) : null}
        {this.state.viewLink && this.state.selectedLink ? (
          <ViewLink 
          closeLinkStats={this.viewLinkStats}
          data={this.state.selectedLink} />
        ) : null}

        {this.state.curator ? (
          <div className="dashboard">
            {/* Left Panel */}
            <div className="dashboard-left">
              <div className="dashboard-left-topBar">
                <div className="dashboard-left-topBar-logo">
                  <img src={require("../../images/logo.png")} alt="logo" />
                </div>
                <div className="dashboard-left-topBar-search">
                  <input
                    type="text"
                    name="search"
                    id="link-searchBar"
                    placeholder="Search Links"
                    onChange={this.searchLinksChange}
                    value={this.state.search}
                  />
                  <button onClick={this.searchLinks}>
                    {" "}
                    <FontAwesomeIcon icon={faSearch}  />
                  </button>
                </div>
              </div>
              <div className="dashboard-left-add">
                <input
                  type="text"
                  name="add"
                  id="add"
                  placeholder="Paste your Link here to shorten"
                  value={this.state.newLink}
                  onChange={(e)=>this.setState({newLink : e.target.value})}
                />
                <button onClick={this.addLink}>GO</button>
              </div>
              <div className="dashboard-left-list">
                <div className="dashboard-left-list-head">ALL LINKS</div>
                <ul>
                
                  {!this.state.searchLinks.length > 0 && this.state.myLinks.map(linkData=>{
                    return <li onClick={()=>this.viewLinkStats(linkData)} >
                      {linkData.longUrl}
                      <span>{linkData.shortUrl}</span>
                    </li>
                  })}

                  {
                    this.state.searchLinks.length > 0 && this.state.searchLinks.map(linkData=>{
                      return <li onClick={()=>this.viewLinkStats(linkData)} >
                      {linkData.longUrl}
                      <span>{linkData.shortUrl}</span>
                    </li>
                    })
                  }
                </ul>
              </div>
            </div>
            {/* DIVIDER */}
            <div className="dashboard-divider">
              <div className="dashboard-divider-line" />
            </div>

            {/* right panel */}
            <div className="dashboard-right">
              <div className="dashboard-right-profile">
                <div className="dashboard-right-profile-image">
                  <img
                    src={
                      "http://flawless-psyche-243712.appspot.com" +
                      this.state.curator.avatar
                    }
                    alt="Avatar"
                  />
                </div>
                <div className="dashboard-right-profile-name">
                  {this.state.curator.name}
                </div>
                <div className="dashboard-right-profile-type">
                  {this.state.curator.typeOfUser}
                </div>
              </div>
              <ul className="dashboard-right-stats">
                <li>
                  <div className="dashboard-right-stats-icon">
                    <FontAwesomeIcon icon={faChartLine} />
                  </div>
                  <div className="dashboard-right-stats-count">{totalViews}</div>
                  <div className="dashboard-right-stats-name">Total Views</div>
                </li>
                <li>
                  <div className="dashboard-right-stats-icon">
                    <FontAwesomeIcon icon={faCompass} />
                  </div>
                  <div className="dashboard-right-stats-count">77</div>
                  <div className="dashboard-right-stats-name">Top Location</div>
                </li>{" "}
                <li>
                  <div className="dashboard-right-stats-icon">
                    <FontAwesomeIcon icon={faLink} />
                  </div>
                  <div className="dashboard-right-stats-count">{this.state.myLinks.length}</div>
                  <div className="dashboard-right-stats-name">Total Links</div>
                </li>
              </ul>
              <div className="dashboard-right-topLinks-title">Top Links</div>
              <ul className="dashboard-right-topLinks">
                {/* <li className="dashboard-right-topLinks-item">
                  Top Links <span>77</span>{" "}
                </li> */}
                {
                  this.topLinks().map((linkData,i)=>{
                    if(i<5)
                    return <li className="dashboard-right-topLinks-item" onClick={()=>this.viewLinkStats(linkData)}>
                  {linkData.shortUrl.substring(0, 27)} <span>{linkData.views}</span>{" "}
                </li>}
                  )
                }
              </ul>

              <div className="dashboard-right-accountLinks">
                {/* <div className="dashboard-right-accountLinks-item">Account</div> */}
                <div
                  className="dashboard-right-accountLinks-item"
                  onClick={this.logout}
                >
                  Logout
                </div>
              </div>
            </div>
          </div>
        ) : null}
      </>
    );
  }
}

const mapStateToProps = state => ({
  curator: state.curator
});

export default connect(
  mapStateToProps,
  { getCurrentCurator ,
  getCuratorLinks,
  addURL,
  logOutCurator
}
)(dashboard);
